import React from'react';
import './App.css';
import 'antd/dist/antd.css';
import {Table,Spin,Space,Typography} from 'antd'
const{Text} = Typography;

function App() {
  const data =[
    {
      name:'John',
      lname:'Sebastian',
      percen:75,
      address:'John@gmail.com',
      key:'1',
      
    },{
      name:'Lewis',
      lname:'Hamilton',
      percen:70,
      address:'Lewis44@gmail.com',
      key:'2'
    },{
      name:'Alex',
      lname:'Albon',
      percen:80,
      address:'Alex9@gmail.com',
      key:'3'
    },
  ]

  const columns=[
    {
      title:'Name',
      dataIndex:'name',
      key:'key',
      render: name =>{
        return <a>{name}</a>
      }
    },


    {
      title:'Percentage(%)',
      dataIndex:'percen',
      key:'key',
      sorter: (a,b) =>a.percen-b.percen
    },
    {
      title:'E-mail ID',
      dataIndex:'address',
      key:'key'
    },
    {
      title: 'Recuited?',
      key:'key',
      render: payload=>{
        return<p>{payload.percen>70 ? 'Selected': 'Not Selected'}</p>
      }
    }

  ]


  return (
    <div className="App">
      
      <header className="App-header">
      
    <Spin size="small" />
    
    <Space direction="vertical">
    <Text type="success" >RESULTS ARE OUT!!</Text>
    </Space>
  
    <Spin size="large" />

  
        <div className="tab">
        <Table
          dataSource={data}
          columns={columns}
          className="tab1"
        />
        </div>
      </header>
      
    </div>
  );
}

export default App;
